<template>
	<div>
		<ContentHeader3 title="Tindakan" url="/tindakan" title2="Data Tindakan" url2="/data-tindakan" subTitle="Edit Data Tindakan" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="row justify-content-center">
		            <div class="col">
		            	<div class="card">
		            		<div class="card-header row">
		            			<h3 class="card-title">Edit Data Tindakan</h3>
		            		</div>
		            		<div class="card-body">
		            			<form @submit.prevent="updateDataTindakan">
					                <div class="px-lg-2 ">
					                  <div class="form-group">
					                    <label for="kode_tindakan">Kode</label>
					                    <input type="text" class="form-control" id="kode_tindakan" name="kode_tindakan" disabled="" v-model="tindakan.kode_tindakan">
					                  </div>
					                  <div class="form-group">
					                    <label for="nama">Nama <span class="text-danger">*</span></label>
					                    <input type="text" class="form-control" id="nama" required="" name="nama" placeholder="Nama Tindakan" autocomplete="off" v-model="tindakan.nama">
					                  </div>
					                  <div class="form-group">
					                    <label for="jenis_tindakan">Jenis <span class="text-danger">*</span></label>
					                    <select class="custom-select form-control" v-model="tindakan.jenis_tindakan">
      										<option selected></option>
      										<option value="laser">Laser</option>
      										<option value="chemical peeling">Chemical Peeling</option>
      										<option value="thread">Thread</option>
      										<option value="konsultasi">Konsultasi</option>
      										<option value="injection">Injection</option>
      										<option value="surgery">Surgery</option>
      										<option value="serum treatment">Serum Treatment</option>
      										<option value="facial">Facial</option>
      										<option value="spa">Spa</option>
	    								</select>
					                  </div>
					                  <div class="form-group">
					                    <label for="lama_tindakan">Durasi <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Menit</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" required="" id="lama_tindakan" name="lama_tindakan" placeholder="Durasi Tindakan" v-model="tindakan.lama_tindakan">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="lama_tindakan">Tarif <span class="text-danger">*</span></label>
					                    <div class="row">
					                    	<div class="col">
					                    		<span>Umum</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="harga_pasien_baru" v-model="tindakan.harga_pasien_baru">
								                </div>
					                    	</div>
					                    	<div class="col">
					                    		<span>Member</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="harga_pasien_member" v-model="tindakan.harga_pasien_member">
								                </div>
					                    	</div>
					                    </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="modal">Modal <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Rp.</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="modal" required="" name="modal" placeholder="Modal" v-model="tindakan.modal">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="fee_dokter">Fee Dokter <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">{{ nominalDokter }}</div>
						                    <button type="button" class="btn btn-secondary btn-flat dropdown-toggle dropdown-icon" data-toggle="dropdown">
						                      <span class="sr-only">Toggle Dropdown</span>
						                    </button>
						                    <div class="dropdown-menu" role="menu">
						                      <a class="dropdown-item" href="#" @click="changeNominalDokter()">Rupiah</a>
						                      <a class="dropdown-item" href="#" @click="changeNominalDokter()">Persen (%)</a>
						                    </div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="fee_dokter" required="" name="fee_dokter" placeholder="Fee Dokter" v-model="tindakan.fee_dokter" @keyup="limitValueNominalDokter(tindakan.fee_dokter)">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="point">Point</label>
					                    <input type="number" class="form-control" id="point" name="point" placeholder="Point" v-model="tindakan.point">
					                  </div>
					                  <button type="submit" class="btn btn-primary mt-3">Submit</button>
					                </div>
					                <!-- /.card-body -->

					                
					               
					              </form>
		            		</div>
		            	</div>
		            </div>
		        </div>
			</div>
		</div>
	</div>
</template>

<script>
import ContentHeader3 from '@/components/ContentHeader3'

import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import axios from 'axios'
import router from '@/router'
import Swal from 'sweetalert2'

export default{
	components:{
		ContentHeader3: ContentHeader3
	},
	setup(){
		const route = useRoute()
		const tindakan_id = route.params.id

		const { tindakan, nominalDokter, getTindakanWhereId, updateDataTindakan } = useUpdateTindakan(tindakan_id)

		const { changeNominalDokter, limitValueNominalDokter } = useInputPersen(tindakan, nominalDokter )
		

		onMounted(() => {
			getTindakanWhereId()
		})

		return {
			tindakan, 
			nominalDokter, 
			updateDataTindakan, 
			changeNominalDokter,
			limitValueNominalDokter,
		}
	}
}


function useUpdateTindakan (tindakan_id) {
		const tindakan = ref([])
		const nominalDokter = ref()
		
		const getTindakanWhereId = async () => {
			let response = await axios.get(`api/tindakan/getWhereId/${tindakan_id}`)
			tindakan.value = response.data

			if (tindakan.value.fee_dokter <= 100) {
				nominalDokter.value = 'Persen (%)'
			} else {
				nominalDokter.value = 'Rp.'
			}

		}

		const updateDataTindakan = () => {
			axios.put(`api/tindakan/update/${tindakan_id}`, tindakan.value)
			.then((response) => {
				Swal.fire({
				  title: 'Berhasil!',
				  text: 'Berhasil mengedit data',
				  icon: 'success',
				  confirmButtonText: 'Ok'
				})	
	    		console.log(response.data)
	    	})
	    	.catch((error) => {
	    		Swal.fire({
				  title: 'Gagal!',
				  text: 'Gagal mengedit data',
				  icon: 'error',
				  confirmButtonText: 'Ok'
				})	
	    		console.log(error)
	    	})	

	    	router.push('/data-tindakan')
		}

		return {
			tindakan,
			nominalDokter,
			getTindakanWhereId,
			updateDataTindakan
		}
}


function useInputPersen (tindakan, nominalDokter) {
		const changeNominalDokter = () => {
			if (nominalDokter.value == 'Rp.') {
				nominalDokter.value = 'Persen (%)'
				tindakan.value.fee_dokter = 0
			} else if (nominalDokter.value == 'Persen (%)') {
				nominalDokter.value = 'Rp.'
				tindakan.value.fee_dokter = 0
			}
		}


		const limitValueNominalDokter = (value) => {

			if (nominalDokter.value == 'Persen (%)' && value > 100) {
				tindakan.value.fee_dokter = 100
			} else if (nominalDokter.value == 'Persen (%)' && value < 0) {
				tindakan.value.fee_dokter = 0
			}
		}

		return {
			changeNominalDokter,
			limitValueNominalDokter
		}
	}


</script>


